import React, {Component}  from 'react';
import {Link} from 'react-router';

import {Header} from '../components/';
import {UserProfiles} from '../components/user';

import {login} from '../api/auth';
import {set} from '../auth/token';

import {isEmpty} from 'lodash';

class Login extends Component {

  state = {
    email: ``,
    password: ``
  }

  componentDidMount() {
    this.$email.focus();
    this.$password.focus();
    this.$error.focus();
  }

  /*handleKeyUp = ({keyCode}) => {

    const {onSubmitUsername} = this.props;
    const {username} = this.state;

    if(keyCode === 13 && username) {
      this.setState({username: ``});
      onSubmitUsername(username);
    }

  }*/

  handleChange = () => {
    this.setState({
      email: this.$email.value,
      password: this.$password.value
    });
  }

  validate() {

    const {email, password} = this.state;

    let error = ``;

    if (!email || !password) {
      error = `Invalid email / password combination`;
    }

    return error;

  }

  submitHandler(e) {

    e.preventDefault();

    const error = this.validate();

    if (!isEmpty(error)) {

      this.setState({error, password: ``, email: ``});

    } else {

      console.log(`login`);

      login(this.state)
        .then(d => set(d))
        .then(() => {
          this.context.router.push(`/families`);
        })
        .catch(err => {
          this.setState({error: err.error, password: ``, email: ``});
        });

    }

  }

  render() {

    const {email, password, error} = this.state;

    return (
      <div className='page page-login'>

        <Header />

        <main>

          <UserProfiles />


          <form action='' method='post'
            acceptCharset='utf-8' onSubmit={e => this.submitHandler(e)}>

            <fieldset>

              <label>E-mail</label>
              <input
                type='text'
                name='email'
                ref={el => this.$email = el}
                value={email}
                onChange={this.handleChange} />

              <label>Password</label>
              <input
                type='password'
                name='password'
                ref={el => this.$password = el}
                value={password}
                onChange={this.handleChange} />

              <div className='error' ref={el => this.$error = el}>{error}</div>

              <button type='submit' className='btn'>Log In</button>

            </fieldset>

          </form>

          <p>Don't have a profile on Tales@Home? <Link to='/register'>Register today!</Link> </p>
          <p><Link to='/'>Forgotten your password?</Link></p>
        </main>

      </div>
    );
  }

}

export default Login;



