import React from 'react';
import {Link} from 'react-router';

const UserProfiles = () => {
  return (
    <ul>
      <li>
        <Link to='Register'>
          new mentor
        </Link>
      </li>
      <li>
        <Link to='Login'>
          mentor
        </Link>
      </li>
      <li>
        <Link to='Join'>
          family
        </Link>
      </li>
    </ul>
  );
};

export default UserProfiles;



